body {
    color: wheat;
    background-color: black;
}

.field5 {
    grid-template-columns: repeat(5, min(15vw, 15vh));
    grid-template-rows: repeat(5, min(15vw, 15vh));
    display: grid;
}

.field5m {
    grid-template-columns: repeat(5, min(10vw, 10vh));
    grid-template-rows: repeat(5, min(10vw, 10vh));
    display: grid;
}

.field4 {
    grid-template-columns: repeat(4, min(10vw, 10vh));
    grid-template-rows: repeat(4, min(10vw, 10vh));
    display: grid;
}

.field6 {
    grid-template-columns: repeat(6, min(10vw, 10vh));
    grid-template-rows: repeat(6, min(10vw, 10vh));
    display: grid;
}

.field7 {
    grid-template-columns: repeat(7, min(10vw, 10vh));
    grid-template-rows: repeat(7, min(10vw, 10vh));
    display: grid;
}

.boxm {
    margin: auto;
    /*border: 1px solid salmon;*/
    width: min(10vw, 10vh);
    height: min(10vw, 10vh);
    display: flex;
    align-items: center;
    justify-content: center;
}

.box {
    margin: auto;
    /*border: 1px solid salmon;*/
    width: min(15vw, 15vh);
    height: min(15vw, 15vh);
    display: flex;
    align-items: center;
    justify-content: center;
}

.start {
    background-color: blueviolet;
}

.end {
    background-color: hotpink;
}

.selected {
    background-color: blueviolet;
}

.container {
    display: flex;
    justify-content: center;
}